import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGlobe } from '@fortawesome/free-solid-svg-icons';
import { FormattedMessage } from 'react-intl';

const Languages = () => {
	return (
		<section className="main-section languages">
			<h2>
				<FontAwesomeIcon className="icon" icon={faGlobe} size="lg" />
				<FormattedMessage id="main.languages.title" defaultMessage="Idiomas" />
			</h2>
			<h3>
				<FormattedMessage id="main.languages.english" defaultMessage="Inglés avanzado:" />
			</h3>
			<h3>2018</h3>
			<p>
				<FormattedMessage
					id="main.languages.uba"
					defaultMessage="Nivel 12 de laboratorio de idiomas. Universidad de Buenos Aires."
				/>
			</p>
			<h3>2009</h3>
			<p>First Certificate in English.</p>
		</section>
	);
};

export default Languages;
