import React, { useContext } from 'react';
import { langContext } from '../../Context/LangContext';
import { FormattedMessage } from 'react-intl';

const LangCV = () => {
	const language = useContext(langContext);

	return (
		<div className="buttons">
			<button className="btn-language" onClick={() => language.setLanguage('es-AR')}>
				<FormattedMessage id="main.language.spanish" defaultMessage="Español" />
			</button>
			<button className="btn-language" onClick={() => language.setLanguage('en-US')}>
				<FormattedMessage id="main.language.english" defaultMessage="Inglés" />
			</button>
		</div>
	);
};

export default LangCV;
