import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faBehanceSquare, faYoutubeSquare } from '@fortawesome/free-brands-svg-icons';
import { FormattedMessage } from 'react-intl';

const PersonalInformation = () => {
	return (
		<section className="personal-information">
			<h2>
				<FormattedMessage id="sidebar.personal-information.title" defaultMessage="Datos Personales" />
			</h2>
			<p>
				<FormattedMessage id="sidebar.personal-information.phone" defaultMessage="Teléfono: 1134205117" />
			</p>
			<p>Mail: eugesaffer@gmail.com</p>
			<p>
				<FormattedMessage
					id="sidebar.personal-information.birthdate"
					defaultMessage="Fecha de nacimiento: 25/03/1993"
				/>
			</p>
			<p>DNI: 37269136</p>
			<p>
				<FormattedMessage
					id="sidebar.personal-information.address"
					defaultMessage="Domicilio: Recoleta, CABA"
				/>
			</p>
			<div className="social-item">
				<FontAwesomeIcon className="icon" icon={faLinkedin} />
				<a href="https://www.linkedin.com/in/eugeniasaffer" target="_blank" rel="noopener noreferrer">
					eugeniasaffer
				</a>
			</div>
			<div className="social-item">
				<FontAwesomeIcon className="icon" icon={faBehanceSquare} />
				<a href="https://www.behance.net/eugeniasaffer" target="_blank" rel="noopener noreferrer">
					eugeniasaffer
				</a>
			</div>
			<div className="social-item">
				<FontAwesomeIcon className="icon" icon={faYoutubeSquare} />
				<a
					href="https://www.youtube.com/channel/UCFuWMkkCpSyMFdxqUwV7gPQ"
					target="_blank"
					rel="noopener noreferrer"
				>
					eugeniasaffer
				</a>
			</div>
		</section>
	);
};

export default PersonalInformation;
