import React, { useState } from 'react';
import { IntlProvider } from 'react-intl';
import SpanishMessages from '../Lang/es-AR.json';
import EnglishMessages from '../Lang/en-US.json';

export const langContext = React.createContext();

export const LangProvider = ({ children }) => {
	let defaultLocale;
	let defaultMessages;

	const lang = localStorage.getItem('lang');

	if (lang) {
		defaultLocale = lang;

		if (lang === 'es-AR') {
			defaultMessages = SpanishMessages;
		} else if (lang === 'en-US') {
			defaultMessages = EnglishMessages;
		} else {
			defaultLocale = 'es-AR';
			defaultMessages = SpanishMessages;
		}
	}

	const [messages, setMessages] = useState(defaultMessages);
	const [locale, setLocale] = useState(defaultLocale);

	const setLanguage = (language) => {
		switch (language) {
			case 'es-AR':
				setMessages(SpanishMessages);
				setLocale('es-AR');
				localStorage.setItem('lang', 'es-AR');
				break;

			case 'en-US':
				setMessages(EnglishMessages);
				setLocale('en-US');
				localStorage.setItem('lang', 'en-US');
				break;

			default:
				setMessages(SpanishMessages);
				setLocale('es-AR');
				localStorage.setItem('lang', 'es-AR');
		}
	};

	return (
		<langContext.Provider value={{ setLanguage: setLanguage }}>
			<IntlProvider locale={locale} messages={messages}>
				{children}
			</IntlProvider>
		</langContext.Provider>
	);
};
