import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSuitcase } from '@fortawesome/free-solid-svg-icons';
import { FormattedMessage } from 'react-intl';

const WorkExperience = () => {
	return (
		<section className="main-section work-experience">
			<h2>
				<FontAwesomeIcon className="icon" icon={faSuitcase} size="lg" />
				<FormattedMessage id="main.work.title" defaultMessage="Experiencia Laboral" />
			</h2>
			<div className="work">
				<h3>2017 - 2018</h3>
				<p>
					<FormattedMessage
						id="main.work.flashstock"
						defaultMessage="Fotógrafa. Flashstock Technology Inc. (empresa creadora de contenido visual para marcas
					internacionales)"
					/>
				</p>
			</div>
			<div className="work">
				<h3>2017</h3>
				<p>
					<FormattedMessage
						id="main.work.latinarab"
						defaultMessage="Fotógrafa voluntaria. LatinArab (7° festival internacional de cine latino árabe)"
					/>
				</p>
			</div>
			<div className="work">
				<h3>2016 - 2017</h3>
				<p>
					<FormattedMessage id="main.work.show" defaultMessage="Fotógrafa de recitales y shows en vivo." />
				</p>
			</div>
		</section>
	);
};

export default WorkExperience;
