import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar } from '@fortawesome/free-solid-svg-icons';
import { FormattedMessage } from 'react-intl';

const Skills = () => {
	return (
		<section className="main-section skills">
			<h2>
				<FontAwesomeIcon className="icon" icon={faStar} size="lg" />
				<FormattedMessage id="main.skills.title" defaultMessage="Habilidades" />
			</h2>
			<div className="skill-container">
				<div className="skill-bar photoshop"></div>
			</div>
			<div className="skill-container">
				<div className="skill-bar after-effects"></div>
			</div>
			<div className="skill-container">
				<div className="skill-bar premiere"></div>
			</div>
			<div className="skill-container">
				<div className="skill-bar office"></div>
			</div>
		</section>
	);
};

export default Skills;
