import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGraduationCap } from '@fortawesome/free-solid-svg-icons';
import { FormattedMessage } from 'react-intl';

const Education = () => {
	return (
		<section className="main-section education">
			<h2>
				<FontAwesomeIcon className="icon" icon={faGraduationCap} size="lg" />
				<FormattedMessage id="main.education.title" defaultMessage="Formación Académica" />
			</h2>
			<div className="education-item">
				<h3>
					<FormattedMessage id="main.education.present" defaultMessage="2019 - Actualidad" />
				</h3>
				<p>
					<FormattedMessage
						id="main.education.una"
						defaultMessage="Cursando Licenciatura en Artes Audiovisuales. Universidad Nacional de las Artes."
					/>
				</p>
			</div>
			<div className="education-item">
				<h3>2020</h3>
				<p>
					<FormattedMessage
						id="main.education.marketing"
						defaultMessage="Curso de introducción al marketing digital. Educación IT."
					/>
				</p>
			</div>
			<div className="education-item">
				<h3>2019</h3>
				<p>
					<FormattedMessage
						id="main.education.excel"
						defaultMessage="Curso Excel fundamentos. Educación IT."
					/>
				</p>
			</div>
			<div className="education-item">
				<h3>2018</h3>
				<p>
					<FormattedMessage
						id="main.education.video-editing"
						defaultMessage="Curso de edición de video. CETAE."
					/>
				</p>
			</div>
			<div className="education-item">
				<h3>2015 - 2017</h3>
				<p>
					<FormattedMessage
						id="main.education.photographer"
						defaultMessage="Recibida de Fotógrafa profesional. Instituto Sudamericano para la Enseñanza de la Comunicación."
					/>
				</p>
			</div>
			<div className="education-item">
				<h3>2017</h3>
				<p>
					<FormattedMessage
						id="main.education.direction"
						defaultMessage="Workshop de dirección de fotografía. Universidad de Buenos Aires."
					/>
				</p>
			</div>
			<div className="education-item">
				<h3>2016</h3>
				<p>
					<FormattedMessage
						id="main.education.photography-course"
						defaultMessage="Curso de fotografía pericial. Instituto de Ciencias Forenses."
					/>
				</p>
			</div>
		</section>
	);
};

export default Education;
